$s3-color: #ffc721;

$t-color: #006b4f;
$t2-color: #7ce4c4;
$green: #006b00;
/* Tipografía */
$tipo-principal: 'Montserrat', sans-serif;
$tipo-secundaria: 'Comfortaa', cursive;
